.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.cardsContentIncome{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  margin:2rem;
  border:1px solid #e8e8e8;
  border-radius:4px 4px 0 0;
  border-spacing:2px;
  padding:1rem;
}
.singleCard{
  width: 20vw !important;
  flex-grow: 2 !important;
  margin: 1.5 rem !important;
}

.headBanner{
  background-image: url("./assets/FINTAX.png");
  width: 100%;
  height: auto;
}

.margin1rem a{
  margin: 1rem !important;
}
.boxCard{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.singleCard{
  width: 40vw!important;
  flex-grow: 1!important;
  margin: 1rem!important;
}

.displayFlex{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.imgLogo{
  width: 30vw;

}